
import PreloaderLocal from '@/components/PreloaderLocal.vue'
import { key } from '@/store';
import { ProductActions } from '@/store/product/actions';
import { ProductMutations } from '@/store/product/mutations';
import { computed, defineComponent, ref, watch, nextTick} from 'vue'
import { onClickOutside } from '@vueuse/core'
import { useStore } from 'vuex';



export default defineComponent({
	props:{
		modelValue: {
			type: Boolean,
			required: true,
		}
	},
	components:{
		PreloaderLocal
	},
	setup(props, { emit }) {
		const store = useStore(key)
		const search_str = ref('')
		const loading = ref(false)
		const debounce = ref<number|undefined>(undefined)
		const target = ref(null)
		const searchInput = ref<any>(null)
		
		const doSearch = () => {
			clearTimeout(debounce.value)
			loading.value=true;
			debounce.value = setTimeout(() => {
					if (search_str.value.length >=2)
					store.dispatch(ProductActions.SEARCH_PRODUCT_ARTICLE, search_str.value)
							.then(()=>{ setTimeout( ()=> {loading.value=false}, 500)})
							//.finally(() => {loading.value=false})
				}, 500)
		}
		const close = () => {
			search_str.value = ''
			store.commit(ProductMutations.SET_PRODUCT_ARTICLS, [])
			emit('update:modelValue', false)
		}
		onClickOutside(target, () => {close()})
		
		watch( ()=>props.modelValue, () => {
			nextTick(() => {
				searchInput.value.focus();
			});
			
			})

		return {
			search_str,
			loading,
			target,
			searchInput,
			articles: computed(()=>store.getters.getProductArticles),
			doSearch,
			close,
		}
	},
})
