
import { ref, computed, defineComponent, PropType } from 'vue'
import { useStore } from 'vuex'

import AmountInput from '@/components/ui/AmountInput.vue'
import DeleteButton from '@/components/ui/DeleteButton.vue'
import EditButton from '@/components/ui/EditButton.vue'
import SelectInput from '@/components/ui/SelectInput.vue'
import SnackBar from '@/components/ui/SnackBar.vue'

import { key } from '@/store/index'
import { OrderMutations } from '@/store/order/mutations'
import { OrderActions } from '@/store/order/actions'
import { OrderStateOrder, OrderStatePosition } from '@/store/order/types'
import { useRouter } from 'vue-router'

export default defineComponent({
	props: {
		data: {
			type: Object as PropType<OrderStateOrder>,
		},
		companys:{
			type: Array,
		},
	modelValue: {
			type: String,
			required: true
		}
	},
	emits: ['update:modelValue', 'onClick'],
	components: {
		AmountInput,
		DeleteButton,
		EditButton,
		SelectInput,
		SnackBar,
	},
	setup(props, { emit }) {
		const store = useStore(key);
		const router = useRouter();
		const open = ref([]);
		const open_presail = ref([]);
		let error = ref(false);
		const snack = ref(false)
		const snackMsg = ref('')
		const selectCompany = computed( {
			get: () => props.modelValue,
			set: (v) => emit('update:modelValue', v)
		});

		let updOrder = () => {
			store.commit(OrderMutations.CALC_ORDER)
		}
		let removePosition = (presail: boolean, guid: string) => {
			if (presail)
				store.dispatch(OrderActions.REMOVE_POSITION_PRESAIL, guid)
			else
				store.dispatch(OrderActions.REMOVE_POSITION, guid)

		}
		let removeCharacteristic = (presail: boolean, CHAR: OrderStatePosition) => {
			if (presail)
				store.dispatch(OrderActions.REMOVE_CHARACTERISTIC_PRESAIL, CHAR)
			else
				store.dispatch(OrderActions.REMOVE_CHARACTERISTIC, CHAR)
		};
		let onClick = () => {
			if (props.modelValue == '') {
				error.value = true;
				setTimeout(() => {error.value=false;}, 5000);
			}
			emit('onClick');
		}
		const addToDraft = () => {
			if (store.getters.isOrderInDraft){
				store.commit(OrderMutations.DEL_ORDER_FROM_DRAFT, props.data?.id)
				snack.value = true
				snackMsg.value = 'Заказ удален из черновика'
			} else {
				store.commit(OrderMutations.ADD_ORDER_TO_DRAFT)
				snack.value = true
				snackMsg.value = 'Заказ добавлен в черновик'
			}
		}
		const goTo = (to: string) =>{
			router.push('/order/'+to)
		}
		return {
			updOrder,
			removePosition,
			removeCharacteristic,
			onClick,
			addToDraft,
			goTo,
			isOrderInDraft: computed(()=> store.getters.isOrderInDraft),
			selectCompany,
			open,
			open_presail,
			error,
			snack,
			snackMsg
		}
	},
})
